	module.exports = {"data":{"markdownRemark":{"html":"<p>Une certaine controverse avait visiblement pris naissance dans l'église de Corinthe au sujet du don des langues, et ce don était élevé à une telle position d'importance dans l'église que l'apôtre Paul fût poussé à écrire à ce sujet dans sa première lettre à cette église (1 corinthiens 12,13 et 14).</p>\n<p>Lisons ce que dit Paul concernant la position qui revient au don des langues dans l'église :</p>\n<blockquote>\n<p>« Et Dieu a établi dans l’Église <strong>premièrement</strong> des apôtres, <strong>secondement</strong> des prophètes, <strong>troisièmement</strong> des docteurs, <strong>ensuite</strong> ceux qui ont le don des miracles, <strong>puis</strong> ceux qui ont les dons de guérir, de secourir, de gouverner, de parler diverses langues. » <br /> <small>- 1 Corinthiens 12:28</small></p>\n</blockquote>\n<p>Paul cite délibérément le don des langues <strong>en dernier</strong> dans l'ordre qu'il donne ici. Pourtant, dans beaucoup d'églises aujourd'hui, le don des langues est présenté comme étant le don que tout chrétien doit avoir <strong>à tout prix</strong>.</p>","frontmatter":{"title":"Quelle importance occupe le don des langues dans l'église ?"}}},"pathContext":{"slug":"/quelle-importance-occupe-le-don-des-langues-dans-l-eglise/"}}

